import { pluginName } from './constants';

const CKEDITOR = window.CKEDITOR;

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init(editor) {
    const colors = CKEDITOR.tools.get_plugin_config(pluginName, editor);

    /**
     * Clone every element in parentChain (strong/em/u/…) **inside** the provided node,
     * so the final markup looks like <span style="color…"><strong>…</strong></span>.
     */
    function wrapInside(node, parentChain) {
      return parentChain.reduceRight((inner, outer) => {
        const wrapper = new CKEDITOR.dom.element(outer.getName());
        for (const [key, val] of Object.entries(outer.getAttributes())) {
          wrapper.setAttribute(key, val);
        }
        wrapper.append(inner);
        return wrapper;
      }, node);
    }

    /**
     * After CKEditor's native applyStyle inserts <span> **inside** <strong>,
     * lift that span so it wraps the inline formatting tag instead.
     */
    function liftColorSpans(range, colorValue) {
      const walker = new CKEDITOR.dom.walker(range.clone().enlarge(CKEDITOR.ENLARGE_INLINE));
      walker.evaluator = node =>
        node.type === CKEDITOR.NODE_ELEMENT &&
        node.getName() === 'span' &&
        node.getStyle('color') === colorValue;

      let span;
      while ((span = walker.next())) {
        const parent = span.getParent();
        if (!parent || parent.getName() === 'span') continue; // already outer‑most
        span.remove();
        parent.insertBeforeMe(span);      // move span before <strong>
        span.append(parent);              // <span><strong>…</strong></span>
      }
    }

    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init() {
        this.add('default', 'Remove Color', 'Remove Color');
        Object.entries(colors).forEach(([label, val]) => this.add(val, label, label));
      },
      onRender() { this.setValue(''); },

      onClick(value) {
        editor.focus();
        const sel = editor.getSelection();
        if (!sel) return;
        const ranges = sel.getRanges();
        editor.fire('lockSnapshot');

        if (value === 'default') {
          ranges.forEach(range => removeColor(range));
        } else {
          const style = new CKEDITOR.style({ element: 'span', styles: { color: value } });
          editor.applyStyle(style);
          // lift inner spans so <span> wraps the inline formatting tags
          ranges.forEach(r => liftColorSpans(r, value));
        }
        editor.fire('unlockSnapshot');
      }
    });

    /** Remove only colour from the current range, keep all other inline styles. */
    function removeColor(range) {
      const walker = new CKEDITOR.dom.walker(range.clone());
      walker.evaluator = n => n.type === CKEDITOR.NODE_TEXT && n.getAscendant('span', true)?.getStyle('color');
      let text;
      while ((text = walker.next())) {
        const span = text.getAscendant('span', true);
        if (!span) continue;

        // Split text into before/selected/after parts
        const start = text.equals(range.startContainer) ? range.startOffset : 0;
        const end = text.equals(range.endContainer) ? range.endOffset : text.getLength();
        const [before, mid, after] = [text.substring(0, start), text.substring(start, end), text.substring(end)];

        const chain = [];
        let p = text.getParent();
        while (p && !p.equals(span)) { chain.unshift(p); p = p.getParent(); }

        /** helper to insert fragment with given colour flag */
        const insertFragment = (content, keepColor) => {
          if (!content) return;
          const fragNode = new CKEDITOR.dom.text(content);
          let toInsert = wrapInside(fragNode, chain);
          if (keepColor) {
            const colourSpan = new CKEDITOR.dom.element('span');
            colourSpan.setAttribute('style', span.getAttribute('style'));
            colourSpan.append(toInsert);
            toInsert = colourSpan;
          }
          span.insertBeforeMe(toInsert);
        };

        insertFragment(before, true);  // left keeps colour
        // middle loses colour but keeps other styles
        const midChain = wrapInside(new CKEDITOR.dom.text(mid), chain);
        span.insertBeforeMe(midChain);
        insertFragment(after, true);   // right keeps colour

        text.remove();
        if (!span.getChildCount()) span.remove();
      }
    }
  }
});
